import * as React from 'react';
import { PlaceholderWrapper, PlaceholderBtnWrapper } from './styled';

interface IProps {
  children: React.ReactNode;
}


const Placeholder = React.forwardRef((props: IProps, ref: React.Ref<HTMLDivElement>) => {
  return (
    <PlaceholderWrapper ref={ref} {...props}>
      {props.children}
    </PlaceholderWrapper>
  );
});

interface IBtnProps {
  children: React.ReactNode;
  onClick?: (e: React.MouseEvent) => void;
  isSelected?: boolean;
  isReader?: boolean;
}

export const BtnWrapper: React.FC<IBtnProps> = (props: IBtnProps) => {
  const { onClick, isReader, isSelected, ...rest } = props;
  return (
    <PlaceholderBtnWrapper isReader={!!isReader} onClick={onClick} isSelected={!!isSelected} {...rest}>
      {props.children}
    </PlaceholderBtnWrapper>
  );
};

export default Placeholder;
